import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'

import Navbar from './components/NavBar';
import Header from './components/Headers';
import Footer from './components/Footer';
import About from './components/about/About';
import Form from './components/Form';
import Construccion from './components/Construccion.js';
import Login from './components/Login';
import Proyectos from './components/proyectos/Proyectos';
import axios from 'axios';

class RouterMain extends Component {
    state = { 
        check:true,
        pixabay:[],
        data:[]
         }
     componentDidMount(){
         this.getPixabay();
         this.getJsonPlaceHolder();
     }

     getPixabay=async()=>{
        const url="https://pixabay.com/api/?key=13398294-d0905e7686f8e8a43e3e2c615&q=city&image_type=photo";
        const pixaBay = await axios.get(url);
        
        
        this.setState({
            pixabay:pixaBay.data.hits
        })   

     }
     getJsonPlaceHolder=async()=>{
        const url="https://jsonplaceholder.typicode.com/posts";
        const jsonplaceholder = await axios.get(url);    
        this.setState({
            data:jsonplaceholder.data
        })  

     }


     HeaderChange=(check)=>{
         const lastCheck = this.state.check;
         if(lastCheck !==check){
             this.setState({
                 check:lastCheck
             })
         }

        

     }
    render() { 
        
     
        
        return (
            
            <Router>
            <Navbar
                HeaderChange
            />
            <Header
                              
            />
            
            <Switch>
                <Route exact path="/about" component={About}/>
                <Route exact path="/portafolio" render={()=>(
                    <Proyectos
                    data={this.state.data}
                    pixabay={this.state.pixabay}
                    

                    />
                )}/>
                <Route exact path="/contacto" component={Form}/>
                <Route exact path="/login" component={Login}/>
                <Route exact path="/construccion" component={Construccion}/>
                

            </Switch>
            <Footer/>
            </Router>

            
          );
    }
}
 
export default RouterMain;

